import React from "react";
import {
  BsFillArchiveFill,
  BsFillGrid3X3GapFill,
  BsPeopleFill,
  BsFillBellFill,
} from "react-icons/bs";
import {
  BarChart,
  Bar,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  LineChart,
  Line,
} from "recharts";

function Home() {
  const data = [
    {
      name: "",
      uv: 4000,
      pv: 2400,
      amt: 2400,
    },
    {
      name: "",
      uv: 3000,
      pv: 1398,
      amt: 2210,
    },
    {
      name: "",
      uv: 2000,
      pv: 9800,
      amt: 2290,
    },
    {
      name: "",
      uv: 2780,
      pv: 3908,
      amt: 2000,
    },
    {
      name: "",
      uv: 1890,
      pv: 4800,
      amt: 2181,
    },
    {
      name: "",
      uv: 2390,
      pv: 3800,
      amt: 2500,
    },
    {
      name: "",
      uv: 3490,
      pv: 4300,
      amt: 2100,
    },
  ];

  return (
    <main className="main-container">
      <div className="main-title">
        <h3 id="new">DASHBOARD</h3>
      </div>

      <div className="main-cards">
        <div className="card">
          <div className="card-inner">
            <h3>EARNINGS</h3>
            <BsFillArchiveFill className="card_icon" />
          </div>
          <h1>$198k</h1>
        </div>
        <div className="card">
          <div className="card-inner">
            <h3>ORDERS</h3>
            <BsFillGrid3X3GapFill className="card_icon" />
          </div>
          <h1>$2.4k</h1>
        </div>
        <div className="card">
          <div className="card-inner">
            <h3>BALANCE</h3>
            <BsPeopleFill className="card_icon" />
          </div>
          <h1>$2.4k</h1>
        </div>
        <div className="card">
          <div className="card-inner">
            <h3>TOTAL SALES</h3>
            <BsFillBellFill className="card_icon" />
          </div>
          <h1>$89k</h1>
        </div>
      </div>

      <div className="charts">
        <ResponsiveContainer width="100%" height="100%">
          <BarChart
            width={500}
            height={300}
            data={data}
            margin={{
              top: 5,
              right: 30,
              left: 20,
              bottom: 5,
            }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="name" />
            <YAxis />
            <Tooltip />
            <Legend />
            <Bar dataKey="MONTH" fill="#8884d8" />
            <Bar dataKey="uv" fill="#82ca9d" />
          </BarChart>
        </ResponsiveContainer>

        <ResponsiveContainer width="100%" height="100%">
          <LineChart
            width={500}
            height={300}
            data={data}
            margin={{
              top: 5,
              right: 30,
              left: 20,
              bottom: 5,
            }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="name" />
            <YAxis />
            <Tooltip />
            <Legend />
            <Line
              type="monotone"
              dataKey="pv"
              stroke="#8884d8"
              activeDot={{ r: 8 }}
            />
            <Line type="monotone" dataKey="MONTH" stroke="#82ca9d" />
          </LineChart>
        </ResponsiveContainer>
      </div>
    </main>
  );
}

export default Home;
